<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">值日生设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="choose">管理</span>
        </div>
        <van-popup v-model="show" position="top" :overlay="true">
            <div class="cfff f28 dropDown">
                <ul>
                    <li v-for="(item,index) in typeList" :key="index" @click="dutyType(index)">
                        {{item.name}}
                        <img :src="select" :style="{display:item.selected?'block':'none'}">
                    </li>
                </ul>
                <div>
                    <p style="border-right:1px solid #6671f2" @click="show=false">取消</p>
                    <p @click="confirm">确定</p>
                </div>
            </div>
        </van-popup>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <img :src="item.checked?checked:notCheck" @click="isChecked(index)"/>
                    <img class="headPic" :src="item.img"/>
                    <span>{{item.name}}</span>
                    <span class="onDuty f20 cfff ml-20" :style="{display:item.checked?'block':'none'}">值日生</span>
                </li>
            </ul>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="issue">设置为值日生</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            notCheck:require('../../assets/message/notCheck.png'),
            checked:require('../../assets/message/checked.png'),
            select:require('../../assets/class/selected.png'),
            show:false,
            list:[
                {
                    checked:true,
                    name:'张三丰',
                    img:require('../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'金毛狮王',
                    img:require('../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'金轮法王',
                    img:require('../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'杨过',
                    img:require('../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'小龙女',
                    img:require('../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'金毛狮王',
                    img:require('../../assets/message/onShow.png'),
                },
            ],
            typeList:[
                {
                    selected:true,
                    name:'擦黑板',
                },{
                    selected:false,
                    name:'地面扫',
                },{
                    selected:false,
                    name:'物品摆放',
                },{
                    selected:false,
                    name:'倒垃圾',
                },{
                    selected:false,
                    name:'擦黑板',
                },{
                    selected:false,
                    name:'地面扫',
                },
            ]
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        issue(){

        },
        isChecked(index){
            var list=this.list;
            if(!list[index].checked){
                for(var i in list){
                    list[i].checked=false;
                }
            }   
            list[index].checked=true;
            this.list=list;
            
        },
        choose(){
            this.show=!this.show;
        },
        confirm(){
            this.show=false;
        },
        dutyType(index){
           var typeList=this.typeList;
            if(!typeList[index].selected){
                for(var i in typeList){
                    typeList[i].selected=false;
                }
            }   
            typeList[index].selected=true;
            this.typeList=typeList; 
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
        z-index: 3001;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
    .content{
        line-height: 1rem;
    }
    li img:first-child{
        width:.4rem;
        height:.4rem;
        margin-right: .3rem;
    }
    .headPic{
        width:.6rem;
        height:.6rem;
        border-radius: 50%;
        margin-right: .3rem;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
    .van-popup{
        padding-top:0.84rem;
        background:#6B77FF;
    }
    .dropDown li{
        position: relative;
        text-align: center;
        display: block;
        height:1rem;
        line-height: 1rem;
        border-bottom: 1px solid #6671f2;
    }
    .dropDown img{
        width:.3rem!important;
        height:.3rem!important;
        position: absolute;
        left:65%;
        /* z-index: 2500; */
        top:.35rem;
    }
    .dropDown div{
        display: flex;
        line-height: .6rem;
        padding:.2rem 0;
        justify-content: space-between;
    }
    .dropDown div p{
        width:50%;
        text-align: center;
    }
</style>




